import React from "react";
import styles from "../../styles/Who.module.css";
import levseta from "../../images/who/v-seta.svg";
import timeline from "../../images/who/timeline.svg";
import celularAzul from "../../images/who/celular-azul.png";
import celularLaranja from "../../images/who/celular-laranja.png";

function Who() {
  const {
    mainSection,
    section2,
    section2Title,
    section3,
    section4,
    section4Content,
    section4ContentWrapper,
    whoMain,
    button,
  } = styles;

  return (
    <main className={whoMain}>
      <section className={mainSection}>
        <h1>Quem somos</h1>
      </section>

      <section className={section2}>
        <p className={section2Title}>
          A Lev chegou ao Brasil para engrandecer o mercado de mobilidade
          urbana.
        </p>
        <p>
          Garantindo liberdade e segurana para nossos parceiros e clientes, o
          objetivo da Lev  ouvir, valorizar e trabalhar com tica,
          transparncia e leveza com os parceiros Lev.
        </p>
        <p>
          Com valores e taxas menores que seus concorrentes, a Lev Moby tem uma
          viso de longo prazo, a qual busca centralizar o mercado brasileiro de
          aplicativos de mobilidade urbana, sempre respeitando os princpios
          ticos e valores do segmento
        </p>
      </section>

      <section className={section3}>
        <h1>Na Lev voc pode aumentar seus ganhos!</h1>
        <p>
          Alm de ser convidativa a quem j atua como motorista em outros
          aplicativos de mobilidade, a Lev se torna uma oportunidade de negcio
          para quem almeja aumentar seus ganhos ou ter seu prprio negcio.
        </p>
        <a href="#" className={button}>
          Saiba mais
        </a>
      </section>

      <section className={section4}>
        <h1>
          <img src={levseta} alt="lev v" /> Histria da Lev.
        </h1>
        <div className={section4ContentWrapper}>
          <img src={timeline} alt="linha do tempo" />
          <div>
            <div className={section4Content}>
              <div>
                <p>
                  A histria da Lev comea em 2018, na cidade de Caldas Novas -
                  GO, quando um dos scios da Moby, nome que a marca tinha
                  naquela poca, notou que a cidade tinha um srio problema de
                  mobilidade urbana. <br />
                  <br />
                  Ao reparar esta necessidade um dos scios visualizou uma
                  grande oportunidade, e assim fundou a Moby. <br />
                  <br />
                  Aps apresentar a Moby em reunio com o prefeito e alguns
                  vereadores, a ideia foi amplamente abraada, todos conseguiam
                  ver a grande oportunidade de novos empregos e avano da
                  mobilidade urbana que o aplicativo poderia trazer para a
                  cidade e regio. <br />
                  <br />
                  Assim sendo a Lev Moby iniciou seus servios e primeiros
                  testes em 2019 na prtica.
                  <br />
                  <br />
                  Ao longo dos anos tanto o aplicativo quanto a empresa foram
                  sendo aprimorados, entrou um novo scio especializado em TI, o
                  que resultou em grandes avanos na parte tcnica do app.{" "}
                  <br />
                  <br />
                  <br />
                  <br />
                </p>
              </div>
              <img src={celularLaranja} alt="celular lev laranja" />
            </div>

            <div className={section4Content}>
              <div>
                <p>
                  Em 2021 um terceiro scio foi captado. Agregando muito
                  conhecimento em estratgias comerciais e aumentando os
                  investimentos, o objetivo da empresa com a entrada desse scio
                  era expandir a plataforma para todo o Brasil. <br />
                  <br />
                  Com todo o planejamento de expanso saindo do papel e com o
                  auxlio de uma Aceleradora de Empresas, o aplicativo teve todo
                  um trabalho de Rebranding e seu nome foi atualizado para Lev
                  Moby, soluo que chegou para solucionar um gargalo de SEO que
                  o nome Moby sozinho carregava. <br />
                  <br />
                  Em 2022 a marca Lev da incio  expanso para todo o Brasil.
                  Com a regio de grande Florianpolis sendo a primeira regio
                  vendida, a captao de usurios e motoristas no litoral
                  catarinense foi iniciada. Agora a empresa busca novos
                  investidores e parceiros para expandir seus servios ainda
                  mais. <br />
                  <br />O mercado de mobilidade urbana no Brasil  enorme e
                  tende a crescer muito mais, a principal meta da Lev 
                  conquistar usurios, motoristas e investidores em todo o
                  territrio brasileiro, a hora  agora
                </p>
              </div>
              <img src={celularAzul} alt="celular lev azul" />
            </div>
          </div>
        </div>
      </section>
    </main>
  );
}

export default Who;
